body {
  font-family: "Montserrat";
}

.map {
  margin-top: 4rem;
}

.envision-title {
  font-size: 900%;
  text-transform: uppercase;
}

.tagline {
  font-size: 2rem;
  color: var(--white);
}

.title-style {
  color: rgb(160, 10, 10);
}

#counter {
  color: var(--white);
  font-size: 10rem;
  text-align: center;
}

.signin-home {
  display: none;
}

.body {
  width: 100%;
  height: 122vh;
  overflow: hidden;
  clip-path: polygon(0 0, 100% 0, 100% 105.5vh, 50% 120.5vh, 0 105.5vh);
  background-color: var(--gold);
  color: var(--white);
  position: relative;
  display: block;
}
.body::after {
  content: "";
  width: 100%;
  height: 120vh;
  position: absolute;
  display: block;
  top: 0;
  left: 0;

  background-size: cover;
  clip-path: polygon(0 0, 100% 0, 100% 105vh, 50% 120vh, 0 105vh);
  background-color: var(--black);
  z-index: -999;
}

.event-start {
  text-align: center;
}

.event-start h1 {
  font-size: 6rem;
  color: rgb(68, 63, 63);
}
.map-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5rem;
}

.hei {
  height: 100vh;
}

.content {
  display: flex;
  flex-direction: row;
  height: 100%;
}

.content-1,
.content-2 {
  flex: 1;
}

.content-1 {
  align-self: flex-start;
  margin-top: 10rem;
  align-items: center;
  text-align: center;
  justify-content: center;
}

.title {
  font-size: 3rem;
  text-transform: uppercase;
  font-weight: 700;
}

.event-desc {
  font-size: 1.6rem;
  list-style: 2rem;
}

.registration {
  margin-top: 3rem;
  margin-bottom: 3rem;
}

.registration a {
  text-decoration: none;
  font-size: 20px;
  color: var(--grey);
  background: var(--dark-blue);
  padding: 10px 30px;
  border-radius: 10px;
}

.event-button {
  font-size: 1.6rem;
}

.event-button-2 {
  margin-top: 3rem;
}

.event-button:hover {
  color: var(--black);
}

@keyframes float {
  0% {
    transform: translate(-50%, -46%);
  }
  50% {
    transform: translate(-50%, -54%);
  }
  100% {
    transform: translate(-50%, -46%);
  }
}
.dj-name {
  color: white;
  text-align: center;
  font-size: 5rem;
}

.dj-rules {
  color: white;
  text-align: center;
  font-size: 1.2rem;
}

.dj-pic {
  height: 250px;
  width: auto;
  text-align: center;
}

@media only screen and (max-width: 500px) {
  .dj-name {
    color: white;
    text-align: center;
    font-size: 3rem;
  }

  .dj-rules {
    color: white;
    text-align: center;
    font-size: 1.2rem;
  }

  .dj-pic img {
    height: 200px;
    width: auto;
    text-align: center;
  }

  .content-1 {
    flex: 1;
  }
  .content-2 {
    flex: 0;
  }
  .content {
    justify-content: center;
    align-items: center;
    text-align: center;
  }

  .signin-home {
    display: block;
    font-size: 1.6rem;
    padding: 0.5rem 1rem;
    margin-top: 2rem;
  }

  .registration a {
    padding: 1rem 1.5rem;
    font-size: 1.6rem;
  }
  .envision-title {
    font-size: 4rem;
  }

  #counter {
    font-size: 4rem;
  }
}

/* Header */
.large-header {
  position: relative;
  width: 100%;
  background: #333;
  overflow: hidden;
  background-size: cover;
  background-position: center center;
  z-index: 1;
}

#large-header {
  background-image: url("https://www.marcoguglie.it/Codepen/AnimatedHeaderBg/demo-1/img/demo-1-bg.jpg");
}

.main-title {
  position: absolute;
  margin: 0;
  padding: 0;
  color: #f9f1e9;
  text-align: center;
  top: 50%;
  left: 50%;
  -webkit-transform: translate3d(-50%, -50%, 0);
  transform: translate3d(-50%, -50%, 0);
}

.demo-1 .main-title {
  text-transform: uppercase;
  font-size: 4.2em;
  letter-spacing: 0.1em;
}

.main-title .thin {
  font-weight: 200;
}

siteTitle {
  display: block;
  font-weight: 900;
  font-size: 30px;
  margin: 20px 0;
}
@media (min-width: 768px) {
  .siteTitle {
    font-size: 60px;
  }
}

main {
  max-width: 960px;
  margin: 0 auto;
  padding: 8rem 0;
}

.mega-card {
  height: 400px;
  position: relative;
  padding: 20px;
  box-sizing: border-box;
  display: flex;
  align-items: flex-end;
  text-decoration: none;
  border: 4px solid var(--gold);
  margin-bottom: 20px;
  background-image: url("https://www.indiaeve.com/images/events/Event324381214Img.jpg");
  background-size: cover;
}
@media (min-width: 768px) {
  .mega-card {
    height: 500px;
  }
}

.inner {
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: white;
  box-sizing: border-box;
  padding: 40px;
}
@media (min-width: 768px) {
  .inner {
    width: 50%;
    height: 100%;
  }
}

.title {
  font-size: 24px;
  color: black;
  text-align: center;
  font-weight: 700;
  color: #181818;
  text-shadow: 0px 2px 2px #a6f8d5;
  position: relative;
  margin: 0 0 20px 0;
}
@media (min-width: 768px) {
  .title {
    font-size: 30px;
  }
}

.subtitle {
  color: #b0215e;
  text-align: center;
}

.card2 {
  background-image: url("https://images.unsplash.com/photo-1496317556649-f930d733eea3?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1950&q=80");
}

.card3 {
  background-image: url("https://images.unsplash.com/photo-1557576842-3bc6093a5085?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1950&q=80");
}

@media only screen and (max-width: 768px) {
  .demo-1 .main-title {
    font-size: 3em;
  }
  .event-start h1 {
    font-size: 4rem;
    color: rgb(68, 63, 63);
  }
}

html {
  background: black;
}

/*DJ*/

.dj {
  background: url(https://i.pinimg.com/originals/44/6e/3b/446e3b79395a287ca32f7977dd83b290.jpg);
  background-size: cover;
  height: 60rem;
}

.firefly {
  position: fixed;
  left: 50%;
  top: 50%;
  width: 0.4vw;
  height: 0.4vw;
  margin: -0.2vw 0 0 9.8vw;
  animation: ease 200s alternate infinite;
  pointer-events: none;
}
.firefly::before,
.firefly::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  transform-origin: -10vw;
}
.firefly::before {
  background: black;
  opacity: 0.4;
  animation: drift ease alternate infinite;
}
.firefly::after {
  background: white;
  opacity: 0;
  box-shadow: 0 0 0vw 0vw yellow;
  animation: drift ease alternate infinite, flash ease infinite;
}

.firefly:nth-child(1) {
  animation-name: move1;
}
.firefly:nth-child(1)::before {
  animation-duration: 15s;
}
.firefly:nth-child(1)::after {
